/* put together one single project card, functional component */
import React from "react";

const ProjectCard = (props) => {
  return (
    <div className="card col-12 col-md-5 col-lg-3 m-2">
      <img className="card-img-top mt-2" src={props.image} alt={props.name} />
      <div className="card-body text-left">
        <h5 className="card-title">{props.name}</h5>
        <p className="card-text">{props.description}</p>
        <p className="card-text">Built with: {props.builtWith}</p>
        <a href={props.link} target="_blank" rel="noopener noreferrer" className="btn view-project-button">
          View
        </a>
      </div>
    </div>
  );
};

export default ProjectCard;
